import React, { useState, useEffect } from 'react';
import articleContent from './article-content';
import ArticlesList from '../components/ArticlesList';
import CommentsList from '../components/CommentsList';
import NotFoundPage from '../pages/NotFoundPage';
import UpvotesSection from '../components/UpvotesSection';
import AddCommentForm from '../components/AddCommentForm';


const ArticlePage = ({ match }) => {
    const name      = match.params.name;
    const article   = articleContent.find(article => article.name === name);

    //With this notation we are defining ArticleInfo with useState as a Constructor. setArticleInfo is the function we call to set the values to articleInfo
    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });

    /*
    Defining an annonymous function that will be called whenever our component is loaded
    If we pass an empty array to useEffect, the function gets called only once (when it's loaded), as it is not watching for any value if it gets changed
    However, if we pass a variable name, then, the function gets called whenever that variable changes. In this case when whe change the article name
    */
    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body  = await result.json();
            console.log(body);
            setArticleInfo(body);
        }

        fetchData();
    }, [name]);

    //if(!article) return <h1>The article "{name}" does not exist!</h1>
    if(!article) return <NotFoundPage />

    const otherArticles = articleContent.filter(article => article.name !== name);

    return (
        <>
            <h1>{article.title}</h1>
            <UpvotesSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
            {article.content.map((paragraph, key)  => (
                <p key={key}>{paragraph}</p>
            ))}
            <CommentsList comments={articleInfo.comments} /> 
            <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
            <h3>Other Articles:</h3>
            <ArticlesList articles={otherArticles} />
        </>
    );
};

export default ArticlePage;